/**
 * Icons absolute
 Just add more padding-left to your parent.
 */
.icon-absolute {
  position: relative; }
  .icon-absolute:before {
    position: absolute;
    left: 0;
    margin: 0;
    top: 50%;
    margin-top: -0.5em; }

