.container-fluid .container-inner {
  max-width: 1400px;
  margin: 0 auto; }

.container-fluid .col {
  padding-top: 20px;
  padding-bottom: 20px; }

/**
 * Sections
 */
.section-highlighted {
  margin-top: 180px;
  margin-bottom: 110px;
  text-align: center; }

.section-curve {
  margin-top: 40px;
  margin-bottom: 40px; }
  .section-curve .container-inner {
    position: relative; }
    .section-curve .container-inner:before, .section-curve .container-inner:after {
      content: "";
      display: block;
      position: absolute;
      left: -20%;
      width: 140%;
      height: 140px;
      z-index: 1; }
    .section-curve .container-inner:before {
      bottom: 100%;
      border-radius: 100% 100% 0 0;
      margin-bottom: -100px; }
    .section-curve .container-inner:after {
      top: 100%;
      border-radius: 0 0 100% 100%;
      margin-top: -100px; }
  .section-curve .row {
    position: relative;
    z-index: 2; }

.section-scnd-bkgd-color {
  background-color: #FAEEB9; }
  .section-scnd-bkgd-color .container-inner:before, .section-scnd-bkgd-color .container-inner:after {
    background-color: #FAEEB9; }

.section-main-bkgd-color {
  background-color: #2E4A70;
  color: #FFFFFF; }
  .section-main-bkgd-color .container-inner:before, .section-main-bkgd-color .container-inner:after {
    background-color: #2E4A70; }
